<template>
  <div class="NDT-Page">
    <h1 class="NDT-Page__Header">
      {{ page.name }}
    </h1>
    <div
      class="NDT-Page__Body"
      v-html="page.body"
    />
    <template v-if="page.type">
      <component :is="page.type" />
    </template>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Tickets from '@/views/Tickets'

  export default {
    name: 'Page',

    components: {
      Tickets
    },

    props: {
      slug: {
        type: String,
        default: null
      }
    },

    computed: {
      ...mapGetters({
        pages: 'pages/pages'
      }),

      page () {
        return this.pages
          .find(({ slug }) => slug === this.slug)
      }
    }
  }
</script>

