<template>
  <div class="NDT-Seat" :class="seatClass" :title="seatTitle" />
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'NDTSeat',

    props: {
      x: {
        type: Number,
        default: null
      },

      y: {
        type: Number,
        default: null
      },

      seatType: {
        type: String,
        default: null
      }
    },

    computed: {
      ...mapGetters({
        seats: 'seatmap/seats'
      }),

      seat () {
        return this.seats
          .find(({ x, y }) => x === this.x && y === this.y)
      },

      seatClass () {
        const { seat, seatType: type } = this
        return {
          'NDT-Seat--Free': type === 'free' || (seat && seat.available && !seat.disabled),
          'NDT-Seat--Taken': type === 'taken' || (seat && !seat.available),
          'NDT-Seat--Disabled': type === 'disabled' || (seat && seat.disabled),
        }
      },

      seatTitle () {
        if (this.seat) {
          if (this.seat.available && !this.seat.disabled) {
            return `Rad: ${ this.seat.row }, Plass: ${ this.seat.seat }`
          }
          if (!this.seat.available && this.seat.owner) {
            return this.seat.owner
          }
        }

        return false
      }
    },
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .NDT-Seat {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: .25rem;

    &--Disabled, &--Free, &--Taken {
      border: 1px solid $dark-gray;
    }

    &--Free {
      cursor: pointer;
      background: #00ff00;
      opacity: .8;
      &:hover {
        opacity: 1;
      }
    }

    &--Taken {
      background: #ff0000;
    }

    &--MySeat {
      background: #0000ff;
    }

    &--Disabled {
      background: gold;
    }
  }
</style>
